import React from "react";
import styles from "../../style";
import { hero } from "../../assets";
import { useTranslation } from "react-i18next";

function Hero() {
  const [t, i18n] = useTranslation();

  return (
    <section
      id="home"
      className={`flex md:flex-row flex-col ${styles.paddingY}`}>
      <div
        className={`flex-1 ${styles.flexStart} flex-col xl:px-0 sm:px-16 px-6`}>
        <div className={`flex flex-row justify-between items-center w-full`}>
          <h1 className="flex-1 font-poppins font-semibold ss:text-[72px] text-[52px] text-white ss:leading-[100px] leading-[75px]">
            {t("Welcome")} <br className="sm:block hidden" />
            <span className="">{t("To")}</span>
          </h1>
        </div>
        <h1 className="text-gradient font-poppins font-semibold ss:text-[68px] text-[52px] text-white ss:leading-[100px] leading-[75px] w-full">
          {t("Wizard Tech")}
        </h1>
        <p className={`${styles.paragraph} max-w-[470px] mt-5`}>
          {t(
            "Our team of experts uses a methodology to identify the credit cards most likely to fit your needs. We examine annual percentage rates, annual fees."
          )}
        </p>
      </div>
      <div
        className={`flex-1 flex ${styles.flexCenter} md:my-0 my-10 relative`}>
        <img
          src={hero}
          alt="wtr-hero"
          className="w-[100%] h-[100%] relative z-[5]"
        />
        <div className="absolute z-[0] w-[40%] h-[30%] top-0 pink__gradient" />
        <div className="absolute z-[0] w-[80%] h-[80%] rounded-full bottom-40 white__gradient" />
        <div className="absolute z-[0] w-[50%] h-[50%] rigth-20 bottom-20 blue__gradient" />
      </div>
    </section>
  );
}

export default Hero;
