import React from "react";
import styles from "../../style";
import "./videobg.css";
import { video } from "../../assets";

function VideoBg() {
  return (
    <section id="videoBg" className={`min-h-screen flex flex-col w-[100%]`}>
      <div
        className={`mt-20 mb-20 md:mb-auto ${styles.flexCenter} w-[100%] h-[100vh]`}>
        <video
          src={video}
          autoPlay
          loop
          muted
          className="object-cover w-[100%] h-[100%]"></video>
      </div>
    </section>
  );
}

export default VideoBg;
